"use client"

import React, { useEffect } from "react"
import type { Metadata } from "next"
import { Geist } from "next/font/google"
import { Analytics } from "@vercel/analytics/next"
import ThemeToggle from "../components/theme-toggle"
import "../globals.css"

// Load Google font
const geist = Geist({ subsets: ["latin"] })

export const metadata: Metadata = {
  title: "Literature Review Generator",
  description: "Generate comprehensive research landscapes powered by AI",
  generator: "v0.app",
  icons: {
    icon: [
      { url: "/icon-light-32x32.png", media: "(prefers-color-scheme: light)" },
      { url: "/icon-dark-32x32.png", media: "(prefers-color-scheme: dark)" },
      { url: "/icon.svg", type: "image/svg+xml" },
    ],
    apple: "/apple-icon.png",
  },
}

export default function RootLayout({
  children,
}: Readonly<{ children: React.ReactNode }>) {
  // Detect system dark mode on client
  useEffect(() => {
    const html = document.documentElement
    const prefersDark = window.matchMedia("(prefers-color-scheme: dark)").matches
    html.classList.toggle("dark", prefersDark)
  }, [])

  return (
    <html lang="en" className={geist.className}>
      <body className="font-sans antialiased bg-background text-foreground transition-colors duration-300">
        {/* Navbar */}
        <header className="flex justify-between items-center px-6 py-4 border-b border-border bg-card">
          <h1 className="text-xl font-bold text-foreground">Literature Review Generator</h1>
          <ThemeToggle />
        </header>

        {/* Main content */}
        <main className="p-6">{children}</main>

        {/* Analytics */}
        <Analytics />
      </body>
    </html>
  )
}
